import React, { useState } from 'react';
import { AppBar, Toolbar, Button, TextField, InputAdornment, Box } from '@mui/material';
import { Link } from 'react-router-dom';
import SearchIcon from '@mui/icons-material/Search';
import Logo from '../images/OctoGigLogo.png'; // Import the logo

function TopBar({ onSearch }) {
  const [searchTerm, setSearchTerm] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    if (onSearch) {
      onSearch(searchTerm);
    }
  };

  return (
    <AppBar position="static" sx={{ backgroundColor: 'transparent', boxShadow: 'none', padding: 0 }}>
      <Toolbar disableGutters sx={{
        justifyContent: 'space-between',
        alignItems: 'center',
        minHeight: '64px',
      }}>
        <Link to="/" style={{ textDecoration: 'none' }}> 
          <img src={Logo} alt="OctoGig Logo" style={{ height: '150px', width:'150px', cursor: 'pointer' }} /> 
        </Link>
        <Box sx={{ flexGrow: 1, display: 'flex', justifyContent: 'flex-start', pl: 2 }}>
          <Button component={Link} to="/about" sx={{ color: 'black', textTransform: 'none', fontFamily: "'Roboto', sans-serif", height: '36px', fontSize: '0.875rem', ml: 2 }}>About</Button>
          <Button component={Link} to="/how-it-works" sx={{ color: 'black', textTransform: 'none', fontFamily: "'Roboto', sans-serif", height: '36px', fontSize: '0.875rem' }}>How it Works</Button>
          <Button component={Link} to="/become-a-seller" sx={{ color: 'black', textTransform: 'none', fontFamily: "'Roboto', sans-serif", height: '36px', fontSize: '0.875rem' }}>Become a Seller</Button>
        </Box>
        <Box sx={{ flexGrow: 1, display: 'flex', justifyContent: 'flex-end', alignItems: 'center' }}>
          <form onSubmit={handleSubmit} style={{ display: 'flex', alignItems: 'center' }}>
            <TextField
              variant="outlined"
              placeholder="Search for freelancers or gigs"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              sx={{
                width: '290px',
                '& .MuiOutlinedInput-root': {
                  height: '36px',
                  '& fieldset': { 
                    borderRadius: '4px',
                  },
                  '& input': {
                    fontSize: '0.875rem',
                  },
                  '&:hover fieldset': {
                    borderColor: '#7851A9', // Hover color for border
                  },
                },
                '& .MuiOutlinedInput-root.Mui-focused fieldset': {
                  borderColor: '#7851A9', // Focus color to match hover
                },
              }}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <Button sx={{ color: 'black', textTransform: 'none', fontFamily: "'Roboto', sans-serif'", padding: '6px', '&:hover': { backgroundColor: '#7851A91A' } }}><SearchIcon /></Button>
                  </InputAdornment>
                ),
              }}
            />
          </form>
          <Button sx={{ color: 'black', textTransform: 'none', fontFamily: "'Roboto', sans-serif", height: '36px', fontSize: '0.875rem', mx: '10px' }}>Log in</Button>
          <Button sx={{ backgroundColor: '#7851A9', color: 'white', textTransform: 'none', fontFamily: "'Roboto', sans-serif", height: '36px', fontSize: '0.875rem', borderRadius: '20px', px: 3, '&:hover': { backgroundColor: '#6a3d9a' } }}>Sign Up</Button>
        </Box>
      </Toolbar>
    </AppBar>
  );
}

export default TopBar;
