import React from 'react';
import Slider from 'react-slick';
import { IconButton, Card, CardContent, Typography, Tooltip, CardMedia, Box } from '@mui/material';
import InstagramIcon from '@mui/icons-material/Instagram'; // Instagram icon
import LanguageIcon from '@mui/icons-material/Language'; // Generic web icon for website
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos'; // Next arrow icon
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos'; // Previous arrow icon
import SGFlag from '../images/SGFlag.png'; // Example flag image, adjust the path as necessary

// Custom arrow for the next button in the slider
function NextArrow(props) {
  const { className, style, onClick } = props;
  return (
    <div
      className={className}
      style={{ ...style, display: 'block', right: '10px', zIndex: 1 }}
      onClick={onClick}
    >
      <ArrowForwardIosIcon />
    </div>
  );
}

// Custom arrow for the previous button in the slider
function PrevArrow(props) {
  const { className, style, onClick } = props;
  return (
    <div
      className={className}
      style={{ ...style, display: 'block', left: '10px', zIndex: 1 }}
      onClick={onClick}
    >
      <ArrowBackIosIcon />
    </div>
  );
}

const GigCard = ({ gig }) => {
  // Slider settings
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    adaptiveHeight: true,
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
  };

  // Function to shorten the description
  const shortenDescription = (description) => {
    const words = description.split(' ');
    return words.length > 10 ? `${words.slice(0, 10).join(' ')}...` : description;
  };

  return (
    <Box sx={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'space-between' }}>
      <Card sx={{
        height: '100%',
        width: '350px',
        display: 'flex',
        flexDirection: 'column',
      }}>
        <Slider {...settings}>
          {gig.images.map((image, index) => (
            <div key={index} style={{ height: '100%', width: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center', paddingTop: '20px' }}>
              <img src={image} alt={`Work ${index + 1}`} style={{ maxHeight: '160px', width: '100%', objectFit: 'cover' }} />
            </div>
          ))}
        </Slider>
        <Box sx={{ display: 'flex', alignItems: 'center', marginTop: '20px', marginLeft: '8px', marginRight: '8px' }}>
          <CardMedia
            component="img"
            image={gig.profilePicture}
            alt={gig.name}
            sx={{ width: 32, height: 32, borderRadius: '50%', marginRight: '10px' }}
          />
          <Typography variant="body2" color="text.primary" sx={{ fontSize: '12px' }}>
            {gig.name}
          </Typography>
        </Box>
        <CardContent sx={{ padding: '0 8px', flexGrow: 1, display: 'flex', flexDirection: 'column', alignItems: 'flex-start', marginTop: '10px' }}>
          <Tooltip title={<Typography sx={{ textAlign: 'left' }}>{gig.description}</Typography>} placement="bottom">
            <Typography variant="body2" color="text.secondary" sx={{ fontSize: '12px', textAlign: 'left', width: '100%' }}>
              {shortenDescription(gig.description)}
            </Typography>
          </Tooltip>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', width: '100%', marginTop: '8px' }}>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              {gig.country === 'Singapore' && (
                <img src={SGFlag} alt="Singapore Flag" style={{ width: 24, height: 24, borderRadius: '50%', marginRight: '5px' }} />
              )}
              <Typography variant="body2" color="text.secondary" sx={{ fontSize: '12px', fontWeight: 'bold' }}>
                {gig.price}
              </Typography>
            </Box>
            <Box>
              {gig.Instagram && (
                <IconButton component="a" href={gig.Instagram} target="_blank" aria-label="Instagram">
                  <InstagramIcon />
                </IconButton>
              )}
              {gig.Website && (
                <IconButton component="a" href={gig.Website} target="_blank" aria-label="Website">
                  <LanguageIcon />
                </IconButton>
              )}
            </Box>
          </Box>
        </CardContent>
      </Card>
    </Box>
  );
};

export default GigCard;